/* Banner*/

.cm-header-wrap {
  z-index: 11;
}

.banner {
  position: relative;
  z-index: 10;
  height: 100vh;
  background-size: cover;
  background-position: center;
}

.banner h1 {
  position: absolute;
    top: 50%;
    left: 50%;
    transform: translate(-50%,-50%);
    font-size: 40px;
    text-align: center;
    background: #f6f8f9;
    padding: 16px;
    border-radius: 5px;
    color: #000;
    font-family: Oswald, sans-serif;
    letter-spacing: 1px;
    width: 700px;
    text-transform: capitalize;
}

.banner h1 a {
  color: #000;
    text-decoration: none;
    text-decoration: underline;
    display: inline-block;
    padding-top: 20px;
    font-size: 45px;
    text-shadow: 9px -1px 6px #000;
  transition: all 300ms ease;
}


.banner h1 a:hover {
    text-shadow: 5px -1px 14px #000;
}
