.registeration-form{
    background-color: #fff;
    position: fixed;
    height: 500px;
    width: 350px;
    left: 50%;
    top: 50%;
    transform: translate(-50%,-50%);
    z-index: 20;
    padding: 20px;
    border-radius: 5px;
    display: none;
  }
  .registeration-form-images{
    display: flex;
    align-items: center;
    justify-content: space-between;
  }
  .close-form{
    height: 15px;
  
  }
  .registeration-form-logo{
    width: 130px;
  }
  .registeration-form-get-started{
    font-size: 20px;
    font-weight: 600;
    font-family: Arial, Helvetica, sans-serif;
    margin-bottom: 0px;
  }
  .registeration-form-continue{
    font-size: 15px;
  }
  .input-mobile{
    border: 0px;
    width: 80%;
    padding-bottom: 5px;
    margin-top: 8px;
  }
  .mobile-form{
    display: flex;
    flex-direction: column;
    align-items: center;
    margin-top: 30px;
    width: 100%;
    border: 1px solid grey;
    border-radius: 10px;
    padding:15px 15px 5px 15px;
  }
  .mobile-from-form{
    width: 100%;
  }
  .Continue-button{
    text-align: center;
    width: 100%;
    color: #fff;
    border: 0px;
    background-color: #21b765;
    padding: 5px 0px;
  }
  .mobile-icon{
    width: 25px;
    height: 25px;
    margin-right: 5px;
  }
  .payment-icon{
    width: 35px;
    height: 35px;
    margin-right: 5px;
  }
  .mail-icon{
    width: 25px;
    height: 25px;
    margin-right: 5px;
  }
  .icon-input{
    display: flex;
    align-items: center;
    flex-wrap: wrap;
    border-bottom: 1px solid #808080;
    margin-bottom: 10px;
    padding-bottom: 5px;
  }
  .icon-input-payment{
    display: flex;
    align-items: center;
    justify-content: center;
    border-bottom: 1px solid #808080;
    margin-bottom: 10px;
    padding-bottom: 5px;
  }
  .form-2,.form-3{
    display: none;
  }
  .dropdown{
    border: 0px;
    
  }
  .categories-option{
    color: #676767;
    font-weight: 100;
  }
  /* .enter-name::after{
    content: "Please Enter the name";
    color: red;
    position: relative;
    bottom: 10px;
  } */
  .validation-message {
    display: none;
    color: red;
  }
  
  .icon-input.invalid .validation-message {
    display: block;
  }
  .registerationSection{
    height: 400px;
    display: flex;
    justify-content: center;
    align-items: center;
    width: 90vw;
    text-transform: uppercase;
  }